<template>
  <div class="detail">
    <div class="box">
      <van-nav-bar
        title=""
        left-text="返回"
        right-text=""
        left-arrow
        @click-left="onClickLeft"
        @click-right="onClickRight"
      />
      <div class="content">
        <div class="goods-img"><img :src="proimg" alt=""></div>
        <div class="goods-info">
          <div class="goods-title">{{ proname }}</div>
          <div class="goods-price">￥ {{ price }}</div>
          <p><span style="color:#ff8114">优惠券</span><span>立即领取 >> </span></p>
          <p><span>配送费</span><span>包邮</span></p>
          <p><span>取货方式</span><span>快递</span></p>
          <p><span>选择商品规格</span><span> >> </span></p>
        </div>
        <div class="goods-details">
          <div class="detail-header">———— 继续拖动 查看商品信息 ————</div>
          <div class="detail-evaluate">
            <van-tabs v-model="activeName">
              <van-tab title="详情" name="a">{{ detail }}</van-tab>
              <van-tab title="评论" name="b">
                <ul v-if="coflag" class="comment">
                  <li v-for="item of commentlist" :key="item.commentid">
                      <div class="top">
                          <img :src="item.file" alt="暂无头像">
                          <div class="right">
                              <h4>{{ item.username }}:<van-rate v-model="item.flag" readonly count="3" class="rate" size="15px"/>
                              </h4>
                              <p class="time">{{item.time}}</p>
                          </div>
                      </div>
                      <div class="bottom">
                          <p>{{ item.text }}</p>
                      </div>
                  </li>
                </ul>
              </van-tab>
            </van-tabs>
          </div>
        </div>
      </div>
    </div>
    <footer class="footer">
      <van-goods-action>
        <van-goods-action-icon icon="chat-o" text="客服" @click="onClickIcon" />
        <van-goods-action-icon icon="cart-o" text="购物车" @click="toCart" />
        <van-goods-action-button type="warning" text="加入购物车" @click="addCart()" />
        <van-goods-action-button type="danger" text="立即购买" @click="buy()" />
      </van-goods-action>
    </footer>
  </div>
</template>

<script>
// import Header from '@/components/Header'
import { GoodsAction, GoodsActionIcon, GoodsActionButton, NavBar, Tab, Tabs, Toast, Dialog } from 'vant'
import axios from 'axios'
import Vue from 'vue'
Vue
  .use(GoodsAction)
  .use(GoodsActionIcon)
  .use(GoodsActionButton)
  .use(Toast)
  .use(Dialog)
Vue.use(NavBar)
Vue.use(Tab).use(Tabs)
export default {
  components: {
    // Header
  },
  data () {
    return {
      userid: '',
      flag: true,
      proname: '',
      proid: '',
      proimg: '',
      detail: '',
      price: '',
      activeName: 'a'
    }
  },
  methods: {
    back () {
      this.$router.back()
    },
    // addCart () {
    //   let userid = localStorage.getItem('userid')
    //   let proid = this.proid
    //   let num = 1
    //   if (userid) {
    //     let url = '/cart/add?userid=' + userid + '&proid=' + proid + '&num=' + num
    //     axios.get(url).then(res => {
    //       // 如果未登录，跳转到登陆
    //       console.log(res.data.code)
    //       if (res.data.code === '10119') {
    //         this.$router.push('/login')
    //       } else {
    //         Toast('加入购物车成功')
    //       }
    //     })
    //   } else {
    //     Dialog.alert({
    //       message: '您还没有登录，请前往登录'
    //     })
    //     this.$router.push('/login')
    //   }
    // },
    onClickLeft () {
      this.$router.back()
    },
    addCart () {
      let userid = localStorage.getItem('userid')
      let proid = this.proid
      let num = 1
      if (userid) {
        let url = '/cart/add?userid=' + userid + '&proid=' + proid + '&num=' + num
        axios.get(url).then(res => {
          // 如果未登录，跳转到登陆
          console.log(res.data.code)
          if (res.data.code === '10119') {
            this.$router.push('/login')
          } else {
            Toast('加入购物车成功')
          }
        })
      } else {
        Dialog.alert({
          message: '您还没有登录，请前往登录'
        })
        this.$router.push('/login')
      }
    },
    toCart () {
      this.$router.push('/cart')
    },
    buy () {
      let userid = localStorage.getItem('userid')
      let proid = this.proid
      let num = 1
      if (userid) {
        let url = '/cart/add?userid=' + userid + '&proid=' + proid + '&num=' + num
        axios.get(url).then(res => {
          // 如果未登录，跳转到登陆
          if (res.data.code === '10119') {
            this.$router.push('/login')
          } else {
            this.$router.push('/cart')
          }
        })
      } else {
        Dialog.alert({
          message: '您还没有登录，请前往登录'
        })
        this.$router.push('/login')
      }
    }
    // buy () {
    //   let userid = localStorage.getItem('userid')
    //   let proid = this.proid
    //   let num = 1
    //   if (userid) {
    //     let url = '/cart/add?userid=' + userid + '&proid=' + proid + '&num=' + num
    //     axios.get(url).then(res => {
    //       // 如果未登录，跳转到登陆
    //       if (res.data.code === '10119') {
    //         this.$router.push('/login')
    //       } else {
    //         this.$router.push('/cart')
    //       }
    //     })
    //   } else {
    //     Dialog.alert({
    //       message: '您还没有登录，请前往登录'
    //     })
    //     this.$router.push('/login')
    //   }
    // }
  },
  mounted () {
    let { proid } = this.$route.params
    console.log(proid, this.$route.params)
    axios.get('/api/pro/detail?proid=' + proid).then(res => {
      this.proname = res.data.data.proname
      this.proid = res.data.data.proid
      this.proimg = res.data.data.proimg
      this.detail = res.data.data.detail
      this.price = res.data.data.price
    })
    axios.get('/comment?proid=' + proid).then(res => {
      if (res.data.data.length === 0) {
        this.coflag = false
      } else {
        this.coflag = true
        console.log(res.data.data[0].file)
        this.commentlist = res.data.data
        for (let item of res.data.data) {
          if (!item.file) {
            item.file = 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=188149577,2949073731&fm=26&gp=0.jpg'
          }
        }
      }
    })
  }
}
</script>

<style lang="scss" scoped>
@import '@/lib/reset.scss';
.detail {
  @include rect(100%,100%);
  @include flexbox();
  @include flex-direction(column);
  .box {
    .van-nav-bar {}
    .content {
      width: 100%;
      height: 3.6rem;
      padding: .12rem;
      background: #fff;
      .goods-img {
        display: flex;
        justify-content: center;
        img {
          width: 3.4rem;
          height: 3.4rem;
        }
      }
      .comment {
        li{
          margin-top: 15px;
          .top{
            @include flexbox();
            align-items: center;
            img{
                width: 33px;
                height: 33px;
                border-radius: 50%;
                display: inline-block;
            }
            .right{
              @include flexbox();
              flex-direction: column;
              justify-content: center;
              text-align: left;
              height: 30px;
              margin-left: 9px;
              h4{
                display: flex;
                align-items: center;
                font-size: 17px;
                color: palevioletred;
              }
              p{
                font-size: 10px;
                color: #ccc;
              }
            }
          }
          .bottom{
            p{
                font-size: 12px;
                margin-left: 42px;
                color: black;
                margin-top: 10px;
            }
          }
        }
      }
      .goods-info {
        .goods-title {
          margin-top: .08rem;
          font-size: .16rem;
          line-height: .2rem;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
        }
        .goods-price {
          font-size: .2rem;
          color: red;
        }
        p {
          font-size: .14rem;
          line-height: .42rem;
          border-top: 1px solid #f3f3f3;
          display: flex;
          justify-content: space-between;
          padding: 0 .1rem;
        }
      }
      .goods-details {
        .detail-header {
          font-size: .14rem;
          line-height: .5rem;
          color: #dcdcdc;
          text-align: center;
          margin: .1rem 0;
        }
        .detail-evaluate {
          font-size: .14rem;
          line-break: .18rem;
          .van-tabs {
            margin: .1rem 0;
            .van-tabs__wrap {}
            .van-tabs__content {
              margin: .1rem 0;
            }
          }
        }
      }
    }
  }
}

</style>
